import React from 'react';
import { Section, SectionMiddleElement, SectionMiddleLeft, SectionMiddleRight } from '../Section';
// import srs from '../../assets/pic3.png';
import { LinkIcons } from '../LinkIcons';
import { SecondaryLink } from '../Buttons';
import pouty from '../../assets/pic2.png';

export default function Contact( props ) {
  return (
    <Section
      className={'contact'}
      headerData={{
        name : 'contact',
        title : 'Contact',
        subtitle : 'Let\'s get in touch',
        ...props
      }}
    >
      <SectionMiddleElement>
        <SectionMiddleLeft>
          <h2>Let's work together!</h2>
          <div className={'section-buttons'}>
            <SecondaryLink
              href={'mailto:amovilla1103@gmail.com'}
              text={'Contact Me'}
            />
            <LinkIcons />
          </div>
        </SectionMiddleLeft>

        <SectionMiddleRight>
          <div className={'img-wrapper'}>
            <img className={'contact-img'} src={pouty} alt={'Andrs Movilla'} />
          </div>
        </SectionMiddleRight>
      </SectionMiddleElement>
    </Section>
  );
}
