import React from 'react';
import srs from '../../assets/pic4.png';
import fny from '../../assets/pic5.png';
import { Section, SectionMiddleElement, SectionMiddleLeft, SectionMiddleRight } from '../Section';
import { PrimaryLink } from '../Buttons';
import { ReactComponent as Download } from '../../assets/download.svg';

export default function Intro() {
  const [ showFunny, setFunny ] = React.useState(false);

  React.useEffect(() => {
    const handleClick = () => {
      setFunny(!showFunny);
    };

    const elem = document.getElementsByClassName('burger-btn')[0];
    if ( elem ) {
      elem.addEventListener('click', handleClick);
      return () => elem.removeEventListener('click', handleClick);
    }
  });

  return (
    <Section
      className={'intro'}
      headerData={{name:'intro'}}
    >
      <SectionMiddleElement>
        <SectionMiddleLeft>
          <div className={'titles'}>
            <h1>Hi, I am Andrs</h1>
            <h3>Fullstack developer</h3>
          </div>
          <div>
            Web developer, with extensive knowledge and years of experience, working in web technologies,
            delivering quality work.
          </div>
          <PrimaryLink
            target={'_blank'}
            rel={'noopener noreferrer'}
            href={'CV_Andres_Movilla_Obregon_EN.pdf'}
            text={'Download CV'}
            icon={<Download/>}
          />
        </SectionMiddleLeft>
        <SectionMiddleRight>
          <div className={'img-wrapper'}>
            <img className={'intro-img'} src={srs} alt={'Andrs Movilla'} style={{ transition : '0.1s', opacity : (1 + showFunny) % 2 }} />
            <img className={'intro-img'} src={fny} alt={'Andrs Movilla'} style={{ transition : '0.1s', opacity : +showFunny }} />
          </div>
        </SectionMiddleRight>
      </SectionMiddleElement>
    </Section>
  );
}

