import React from 'react';
import './style/main.scss';
import { Intro, Skills, Experience, Portfolio, Contact } from './components/sections';
import { Footer } from './components/Footer';
import { Header } from './components/Header';
import { SideLinksFloat } from './components/SideLinksFloat';
import { Themed } from './components/Themed';

const sectionColors = [
  '#632bf3',
  '#f122c8',
  '#f16022',
  '#f1c822',
  '#9ef344',
  '#44d3f3',
  '#632bf3',
]

const navItems = [
  {
    name : 'Andrs Movilla',
    direction : 'intro',
    borderColors : sectionColors.slice(0, 2),
    Component : ( { active }) => <Intro
      isActive={active}
      titleColors={sectionColors.slice(0, 2)}
    />
  },
  {
    name : 'Experience',
    direction : 'experience',
    borderColors : sectionColors.slice(1, 3),
    Component : ( { active }) => <Experience
      isActive={active}
      titleColors={sectionColors.slice(1, 3)}
    />
  },
  {
    name : 'Skills',
    direction : 'skills',
    borderColors : sectionColors.slice(2, 4),
    Component : ( { active }) => <Skills
      isActive={active}
      titleColors={sectionColors.slice(2, 4)}
    />
  },
  {
    name : 'Portfolio',
    direction : 'portfolio',
    borderColors : sectionColors.slice(3, 5),
    Component : ( { active }) => <Portfolio
      isActive={active}
      titleColors={sectionColors.slice(3, 5)}
    />
  },
  {
    name : 'Contact',
    direction : 'contact',
    borderColors : sectionColors.slice(4, 6),
    Component : ( { active }) => <Contact
      isActive={active}
      titleColors={sectionColors.slice(4, 6)}
    />
  },
];

const App = () => {
  const [ active, setActive ] = React.useState(0);

  React.useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  });

  const scrollTo = ( { id, pos = 0 } ) => {
    let yPos = id ? window.scrollY + document.getElementById(id)
      .getBoundingClientRect().top - (window.innerWidth <= 640 ? 0 : 70) : pos;

    window.scrollTo({
      top : yPos,
      left : 0,
      behavior : 'smooth'
    });
  };

  const handleScroll = () => {
    const headers = navItems
      .map((item, key ) => ({
        key, position: document.getElementById( item.direction )?.getBoundingClientRect().top
        }))
      .filter(item => !!item.position)
      .filter(item => item.position < window.innerHeight * 0.6)

    setActive(headers[headers.length-1]?.key ?? -1)
  };

  return <Themed>
    <SideLinksFloat />
    <Header
      scroll={scrollTo}
      navItems={navItems}
      activeSection={active}
    />
    {
      navItems.map(
        ( item, key ) =>
          <item.Component active={active===key} key={key} />
      )
    }
    <Footer/>
  </Themed>;
};

export default App;
