import React from 'react';
import { Section, SectionMiddleElement, SectionMiddleSub } from '../Section';
import { ReactComponent as ArrowDown } from '../../assets/arrow-down.svg';
import { ReactComponent as ArrowRight } from '../../assets/arrow-right.svg';

const skills = [
	{
		name : 'Frontend developer',
		length : 'More than 4 years',
		subskills : [
			{
				name : 'HTML',
				value : 85
			},
			{
				name : 'CSS',
				value : 95
			},
			{
				name : 'JavaScript',
				value : 95
			},
			{
				name : 'React',
				value : 85
			},
		]
	},
	{
		name : 'Backend developer',
		length : 'More than 4 years',
		subskills : [
			{
				name : 'NodeJS + Express',
				value : 80
			},
			{
				name : 'MongoDB',
				value : 85
			},
			{
				name : 'NestJS',
				value : 30
			},
		]
	},
	{
		name : 'Developer',
		length : 'More than 7 years',
		subskills : [
			{
				name : 'Python',
				value : 80
			},
			{
				name : 'C++',
				value : 40
			},
			{
				name : 'C#',
				value : 60
			},
			{
				name : 'Lua',
				value : 80
			},
		]
	},
]

export default function Skills( props ) {
	const [ openedSection, setOpenedSection ] = React.useState( 0 )

	return (
		<Section
			className={ 'skills' }
			headerData={{
				name : 'skills',
				title : 'Skills',
				subtitle : 'My technical level',
				...props
			}}
		>
			<SectionMiddleElement>
				{
					skills.map( ( item, i ) =>
						<SectionMiddleSub key={i}>
							<SkillCategory
								openSection={ () => setOpenedSection( i ) } key={ i }
								isOpen={ i === openedSection } { ...item }
							/>
						</SectionMiddleSub>
					)
				}
			</SectionMiddleElement>
		</Section>
	);
}

function SkillCategory( { name, length, subskills, isOpen, openSection } ) {
	return (
		<div className={ 'skill-category' } style={{'--opened-height': `${48*subskills.length}px`}}>
			<div className={ 'title-container' } onClick={ openSection }>
				<h3 className={ 'title' }>
					{ name }
				</h3>
				<div className={ 'subtitle' }>
					{ length }
				</div>
				<div className={ 'arrow-container' }>
					{ isOpen ? <ArrowDown/> : <ArrowRight/> }
				</div>
			</div>
			<ul className={ '' + ( isOpen ? 'opened' : '' ) }>
				{
					subskills.map( ( item, i ) =>
						<li key={ i }>
							<div className={ 'skill-name' }>
								<div className={ 'name' }>
									{ item.name }
								</div>
								<div className={ 'value' }>
									{ `${ item.value }%` }
								</div>
							</div>
							<div className={ 'progress-bar' }>
								<div style={ { width : `${ item.value }%` } } className={ 'progress-bar-fill' }/>
							</div>
						</li>
					)
				}
			</ul>
		</div>
	)
}
