export default function Experience( props ) {
	const [ category, setCategory ] = React.useState( 'wrk' );

	return (
		<Section
			className={ 'experience' }
			headerData={{
				name : 'experience',
				title : 'Experience',
				subtitle : 'My personal journey',
				...props
			}}
		>
			<SectionTopElement>
				<SecondaryButton
					style={{flex: 1}}
					text={ 'Work' }
					icon={<Code/>}
					onClick={ () => setCategory( 'wrk' ) }
				/>
				<SecondaryButton
					style={{flex: 1}}
					text={ 'Education' }
					icon={<School/>}
					onClick={ () => setCategory( 'edu' ) }
				/>
				<SecondaryButton
					style={{flex: 1}}
					text={ 'Recognitions' }
					icon={<Award/>}
					onClick={ () => setCategory( 'rec' ) }
				/>
			</SectionTopElement>
			<SectionMiddleElement>
				{
					{wrk: workItems, edu: educationItems, rec: recogItems}[category]
					.map( ( item, i ) =>
						<ExperienceItem key={ i } index={ i } { ...item } />
					)
				}
			</SectionMiddleElement>
		</Section>
	);
}

function ExperienceItem( { title, subtitle, timestamp, index } ) {
	return (
		<div className={ 'exp-item' }>
				<h3>{ title }</h3>
				<div>{ subtitle }</div>
				<div>{ timestamp }</div>
		</div>
	)
}

