import React from 'react';
// import { ReactComponent as Moon } from '../assets/moon.svg';
// import { ReactComponent as Sun } from '../assets/sun.svg';
// import { ReactComponent as Home } from '../assets/home.svg';
import { HamburgerButton, HeaderButton } from './Buttons';

export function Header( { navItems, scroll, currentTheme, handleThemeChange : changeTheme, activeSection } ) {
  const [ showHeaderMobile, setShowHeaderMobile ] = React.useState(false);

  const handleScroll = ( direction ) => {
    setShowHeaderMobile(false);
    scroll(direction);
  };

  const handleThemeChange = () => {
    setShowHeaderMobile(false);
    changeTheme();
  };

  return <>
    {showHeaderMobile && <div className={'hide-pc hide-tablet header-backdrop'} onClick={() => setShowHeaderMobile(false)} />}
    <header>
      <div className={'header-contents'}>

        <div
          className={'header-title'}
        >
          <HeaderButton
            style={{
              '--border-color-start' : navItems[0].borderColors[0],
              '--border-color-end' : navItems[0].borderColors[1],
            }}
            className={'name-btn' + ( activeSection === 0 ? ' active' : '' )}
            onClick={() => handleScroll({ id : navItems[0].direction })}
            text={navItems[0].name}
          />
          <div className={'header-title-buttons'}>
            <HamburgerButton
              className={'hide-pc'}
              isActive={showHeaderMobile}
              onClick={() => setShowHeaderMobile(!showHeaderMobile)}
            />
            {/*<HeaderButton*/}
            {/*  style={{*/}
            {/*    '--border-color-start' : '#632bf3',*/}
            {/*    '--border-color-end' : '#fff',*/}
            {/*  }}*/}
            {/*  iconSide={'left'}*/}
            {/*  className={'icon-button hide-pc hide-tablet'}*/}
            {/*  onClick={handleThemeChange}*/}
            {/*  icon={currentTheme === 0 ? <Moon /> : <Sun />}*/}
            {/*/>*/}
          </div>
        </div>
        <NavButtons
          showHeaderMobile={showHeaderMobile}
          navItems={navItems.slice(1)}
          handleScroll={handleScroll}
          handleThemeChange={handleThemeChange}
          currentTheme={currentTheme}
          activeSection={activeSection}
        />
      </div>
    </header>
  </>
}

function NavButtons( { showHeaderMobile, navItems, handleScroll, handleThemeChange, currentTheme, activeSection } ) {
  return <>
    <div className={'nav-buttons hide-phone' + (showHeaderMobile ? ' display-header' : '')}>
      {/*<HeaderButton*/}
      {/*  style={{*/}
      {/*    '--border-color-start' : '#fff',*/}
      {/*    '--border-color-end' : '#632bf3',*/}
      {/*  }}*/}
      {/*  className={'icon-button hide-pc'}*/}
      {/*  onClick={() => handleScroll({ pos : 0 })}*/}
      {/*  icon={<Home />}*/}
      {/*/>*/}

      {navItems.map(( item, i ) => (
        <HeaderButton
          className={( activeSection === i+1 ? 'active' : undefined )}
          style={{
            '--border-color-start' : item.borderColors[0],
            '--border-color-end' : item.borderColors[1],
          }}
          onClick={() => handleScroll({ id : item.direction })}
          key={i}
          text={item.name}
        />
      ))}

      {/*<HeaderButton*/}
      {/*  style={{*/}
      {/*    '--border-color-start' : '#fff',*/}
      {/*    '--border-color-end' : '#fff',*/}
      {/*  }}*/}
      {/*  className={'icon-button'}*/}
      {/*  iconSide={'left'}*/}
      {/*  onClick={handleThemeChange}*/}
      {/*  icon={currentTheme === 0 ? <Moon /> : <Sun />}*/}
      {/*/>*/}
    </div>
    <div className={'nav-buttons hide-tablet hide-pc' + (showHeaderMobile ? ' display-header' : '')}>
      <div className={'rainbow-border-container'}>
        {
          navItems.map(( item, i ) => (
            <div
              key={i}
              style={{
                '--border-color-start' : item.borderColors[0],
                '--border-color-end' : item.borderColors[1],
              }}
              className={'pretend-button-border'}
            />
          ))
        }
      </div>

      <HeaderButton
        onClick={() => handleScroll({ pos : 0 })}
        text={'Home'}
      />

      {navItems.map(( item, i ) => (
        <HeaderButton
          onClick={() => handleScroll({ id : item.direction })}
          key={i}
          text={item.name}
        />
      ))}
    </div>
  </>;
}
