import React from 'react';
import { ReactComponent as Burger1 } from '../assets/hamburger-01.svg';
import { ReactComponent as Burger2 } from '../assets/hamburger-02.svg';
import { ReactComponent as Burger3 } from '../assets/hamburger-03.svg';

export const PrimaryButton = ( { iconSide = 'right', ...props } ) => (
  <RegularButton
    className={'primary icon-side-' + iconSide}
    {...props}
  />
);

export const SecondaryButton = ( { iconSide = 'right', ...props } ) => (
  <RegularButton
    className={'secondary icon-side-' + iconSide}
    {...props}
  />
);

export const PrimaryLink = ( { iconSide = 'right', ...props } ) => (
  <RegularLink
    className={'button primary icon-side-' + iconSide}
    {...props}
  />
);

export const SecondaryLink = ( { iconSide = 'right', ...props } ) => (
  <RegularLink
    className={'button secondary icon-side-' + iconSide}
    {...props}
  />
);

const ButtonContents = ( { text, icon } ) => (
  <>
    {text && <div className={'text'}>{text}</div>}
    {icon && <div className={'icon'}>{icon}</div>}
  </>
);

const RegularButton = ( { text, icon, ...props } ) => (
  <button
    {...props}
  >
    <ButtonContents
      text={text}
      icon={icon}
    />
  </button>
);

const RegularLink = ( { text, icon, ...props } ) => (
  <a
    {...props}
  >
    <ButtonContents
      text={text}
      icon={icon}
    />
  </a>
);

export function HamburgerButton( { className, onClick, isActive } ) {
  return (
    <button
      className={'burger-btn ' + (className ? className + ' ' : '') + (isActive ? 'is-menu-open' : '')}
      onClick={onClick}
    >
      <Burger1 />
      <Burger2 />
      <Burger3 />
    </button>
  );
}

export function HeaderButton( { text, icon, onClick, style, iconSide='right', className = '' } ) {
  return (
    <button
      className={'header-button icon-side-' + iconSide + (className ? ' ' + className : '')}
      style={style}
      onClick={onClick}
    >
      <div className={'button-border'} />
      <ButtonContents
        text={text}
        icon={icon}
      />
    </button>
  );
}
