export default function Portfolio(props) {
	const [activeCard, setActiveCard] = React.useState(0)

	return (
		<Section
			className={'portfolio'}
			headerData={{
				name: 'portfolio',
				title: 'Portfolio',
				subtitle: 'Some of my work',
				...props
			}}
		>
			<SectionTopElement>
				{
					[...work_projects, ...game_projects].map((item, i) =>
						<div
							className={'number-dot' + (activeCard === i? ' is-active' : '')}
							key={i}
							onClick={() => setActiveCard(i)}
						>
							{i + 1}
						</div>
					)
				}
			</SectionTopElement>

			<SectionMiddleElement>
				{
					[...work_projects, ...game_projects].map((item, i) =>
						<PortfolioCard visible={activeCard === i} key={i} {...item} />
					)
				}
			</SectionMiddleElement>
		</Section>
	);
}

function PortfolioCard({name, short_name, title, description, img, visible, link, transform, img_type, top, left, style}) {
	return (
		<div className={'portfolio-card' + (visible? ' visible' : '')}>

			<div className={'portfolio-card__image'}>
				<a href={link}>
					<img className={`img-${img_type}`} style={{transform, top, left, ...style }} src={img} alt={short_name} />
				</a>
			</div>

			<div className={'portfolio-card__info'}>
				<h3>{`${title} ${!!name? `(${name})`: ''}`}</h3>
				<div className={'portfolio-card__description'}>{description}</div>
				<PrimaryLink
					href={link}
					text={'View'}
				/>
			</div>

		</div>
	)
}






