import React from 'react';

export function Section( { className='', children, headerData } ) {
	return (
		<section className={ className }>
			<SectionHeader
				{...headerData}
			/>
				{children}
		</section>
	);
}

const SectionHeader = ({name, title, subtitle, titleColors, isActive}) => (
	<>
		{ (!!title || !!subtitle) &&
			<div className={'section-header'}>
			{/* eslint-disable-next-line jsx-a11y/heading-has-content */}
			{name && <h1 className={'section-nav'} id={name} />}
			{title && <h1
				style={{'--startColor': titleColors[0], '--endColor': titleColors[1]}}
				className={'section-title' + (isActive? ' active' : '')}
			>
				{title}
			</h1>}
			{subtitle && <h4>{subtitle}</h4>}
		</div>
		}
	</>
)

export function SectionTopElement({children}) {
	return <div className={'pane__top'}>
		{children}
	</div>
}

export function SectionBottomElement({children}) {
	return <div className={'pane__bottom'}>
		{children}
	</div>
}

export function SectionMiddleElement({children}) {
	return <div className={'pane__middle'}>
		{children}
	</div>
}

export function SectionMiddleLeft({children}) {
	return <div className={'pane__left'}>
		{children}
	</div>
}

export function SectionMiddleRight({children}) {
	return <div className={'pane__right'}>
		{children}
	</div>
}

export function SectionMiddleSub({children}) {
	return <div className={'pane__sub'}>
		{children}
	</div>
}
